body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Wrapper {
  box-sizing: border-box;
  background-color: azure;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#drum-machine {
  border: 2px solid blueviolet;
  background-color: blueviolet;
  border-radius: 25px;
  height: 60%;
  width: 60%;
  display: -webkit-flex;
  display: flex;
}

#keyboard {
  width: 70%;
  height: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
}
#control {
  width: 30%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
}
.drum-pad {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  display: -webkit-flex;

  display: flex;
}
.drum-item {
  border: 1px solid blueviolet;

  border-radius: 25px;
  height: 75%;
  width: 75%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 7vh;
  box-shadow: 2px 2px;
}
#display {
  border: 3px solid rgb(80, 83, 73);
  background-color: rgb(187, 144, 228);
  border-radius: 25px;
  height: 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 4vh;
  text-align: center;
  margin: 4% 4% 4% 0%;
}
.text {
  height: 70%;
  font-size: 1.5em;
}
@media screen and (max-width: 750px) {
  #drum-machine {
    height: 75%;
    width: 75%;
  }
  #display {
    font-size: 1em;
  }
}

@media screen and (max-width: 450px) {
  .Wrapper {
    height: 80vh;
  }
  #drum-machine {
    height: 70%;
    width: 100%;
  }
}
@media screen and (max-height: 400px) {
  #display {
    font-size: 1.2em;
  }
  .text {
    font-size: 1.2em;
  }
 }


:root {
  --main-bg-color: #6893e9; 
  --main-txt-color:  #33518b;
}

#wrapper {
  
  background-color: #6893e9;
  
  background-color: var(--main-bg-color);
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;  
}

#quote-box{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background-color: #33518b;
  background-color: var(--main-txt-color);
  border: 2px solid #6893e9;
  border: 2px solid var(--main-bg-color);
  border-radius: 13%;
  width: 80%;
  height: 80%;
  text-align: center; 
  color: #6893e9; 
  color: var(--main-bg-color);
}

#author {
  font-size: 3em;
  font-family: "Oxygen", sans-serif;
  text-shadow: 2px 2px rgb(1, 1, 46);
}

#text {
  font-size: 2.5em;
  font-family: "Fira Sans", sans-serif;
  margin: 0px 20px;
  line-height: 1;
}

#buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-bottom: 4%;
}
button {
  background-color:#6893e9;
  background-color:var(--main-bg-color); 
  color: #33518b; 
  color: var(--main-txt-color);
  border: 2px solid #6893e9;
  border: 2px solid var(--main-bg-color);
  border-radius: 15%;
  font-size: 2em;
  padding: 10px;
  -webkit-transition-duration: 0.6s; /* Safari */
  transition-duration: 0.6s;
  cursor: pointer;
  outline: 0;
}

#new-quote {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
#new-quote span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

#loading {
  font-size: 4em;
  color: #33518b;
  color: var(--main-txt-color);
}
@media screen and (max-width: 450px) {
  #wrapper {
    height: 80vh;
  }
  #author {
    font-size: 2em;
  }
  #text {
    font-size: 1.8em;
  }
}
@media screen and (max-height: 400px) {
  #author {
    font-size: 2.5em;
  }
  #text {
    font-size: 1.8em;
  }
 }

